"use strict";
import { ChooseProvider } from "components/ReceiveCryptoModal/ChooseProvider";
import { useAccount } from "hooks/useAccount";
import ms from "ms";
import { ProviderConnectedView } from "pages/Swap/Buy/ProviderConnectedView";
import { ProviderConnectionError } from "pages/Swap/Buy/ProviderConnectionError";
import { ContentWrapper } from "pages/Swap/Buy/shared";
import { useCallback, useState } from "react";
import { useModalIsOpen, useToggleModal } from "state/application/hooks";
import { Modal } from "uniswap/src/components/modals/Modal";
import { ModalName } from "uniswap/src/features/telemetry/constants";
import { logger } from "utilities/src/logger/logger";
export function ReceiveCryptoModal() {
  const account = useAccount();
  const toggleModal = useToggleModal(ModalName.ReceiveCryptoModal);
  const isOpen = useModalIsOpen(ModalName.ReceiveCryptoModal);
  const [errorProvider, setErrorProvider] = useState();
  const [connectedProvider, setConnectedProvider] = useState();
  const onClose = useCallback(() => {
    toggleModal();
    setTimeout(() => {
      setErrorProvider(void 0);
      setConnectedProvider(void 0);
    }, ms("500ms"));
  }, [toggleModal, setErrorProvider, setConnectedProvider]);
  if (!account.address) {
    logger.debug("ReceiveCryptoModal", "ReceiveCryptoModal", "Modal opened with invalid state. Closing modal.");
    onClose();
    return null;
  }
  return <Modal name={ModalName.ReceiveCryptoModal} isModalOpen={isOpen} onClose={onClose} maxWidth={420}><ContentWrapper>{errorProvider ? <ProviderConnectionError
    onBack={() => setErrorProvider(void 0)}
    closeModal={onClose}
    selectedServiceProvider={errorProvider}
  /> : connectedProvider ? <ProviderConnectedView closeModal={onClose} selectedServiceProvider={connectedProvider} /> : <ChooseProvider setErrorProvider={setErrorProvider} setConnectedProvider={setConnectedProvider} />}</ContentWrapper></Modal>;
}
