import { Button, Flex, Text, TouchableArea, isWeb, useSporeColors } from "ui/src";
import { X } from "ui/src/components/icons/X";
import { zIndexes } from "ui/src/theme";
import { Modal } from "uniswap/src/components/modals/Modal";
import { openURL } from "uniswap/src/utils/link";
import { logger } from "utilities/src/logger/logger";
export function InfoLinkModal({
  name,
  isOpen,
  showCloseButton,
  icon,
  title,
  description,
  buttonText,
  linkText,
  linkUrl,
  onDismiss,
  onButtonPress,
  onAnalyticsEvent,
  height
}) {
  const colors = useSporeColors();
  const openUniswapURL = async () => {
    if (!linkUrl) {
      return;
    }
    try {
      await openURL(linkUrl);
      onAnalyticsEvent?.();
    } catch (error) {
      logger.error(error, { tags: { file: "InfoLinkModal.tsx", function: "openUniswapURL" } });
    }
  };
  return <Modal backgroundColor={colors.surface1.val} isModalOpen={isOpen} name={name} height={height} onClose={onDismiss}>{showCloseButton && <TouchableArea
    p="$spacing16"
    position="absolute"
    right={0}
    top={0}
    zIndex={zIndexes.default}
    onPress={onDismiss}
  >{isWeb && <X color="$neutral2" size="$icon.16" />}</TouchableArea>}<Flex alignItems="center" gap="$spacing8" mx={isWeb ? "$none" : "$spacing36"} pt="$spacing16">{icon}<Flex centered gap="$spacing8" p="$spacing16"><Text color="$neutral1" variant={isWeb ? "subheading2" : "subheading1"}>{title}</Text><Text color="$neutral2" textAlign="center" variant="body3">{description}</Text></Flex><Flex row width="100%"><Button fill emphasis="secondary" size="large" onPress={onButtonPress}>{buttonText}</Button></Flex>{linkText && linkUrl && <Flex row width="100%"><Button fill emphasis="text-only" size="large" onPress={openUniswapURL}>{linkText}</Button></Flex>}</Flex></Modal>;
}
